.presto-player-divi-editor__inputs {
  list-style: none !important;
  margin: 0 !important;
  padding: 0 !important;
}

.presto-player-divi-editor__inputs li + li {
  margin-top: 20px;
}

.presto-player-divi-editor__inputs label {
  color: #32373c;
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
}
.presto-player-divi-editor__inputs button {
  background-color: #dde4ea;
  color: #5c6978;
  border: none;
  height: 30px;
  line-height: 30px;
  padding: 0px 16px;
  border-radius: 3px;
  cursor: pointer;
}

input.prpl-input {
  background: #f1f5f9;
  max-height: 30px;
  border: 0;
  border-radius: 3px;
  padding: 7px 10px;
  box-sizing: border-box;
  transition: background 200ms ease;
  color: #4c5866;
  font-family: "Open Sans", Helvetica, Roboto, Arial, sans-serif;
  font-size: 13px;
  font-weight: 600;
  line-height: normal;
  display: block;
  width: 100%;
}

input.prpl-input:focus {
  background: #e6ecf2;
}

input.prpl-input::-webkit-input-placeholder {
  color: #98a7b8;
}

input.prpl-input:-moz-placeholder {
  color: #98a7b8;
}

input.prpl-input::-moz-placeholder {
  color: #98a7b8;
}

input.prpl-input:-ms-input-placeholder {
  color: #98a7b8;
}

input.prpl-input[readonly] {
  background: #ffffff !important;
  border: 1px solid #eaedf0 !important;
  cursor: not-allowed;
}
